import BaseFormField from "@/components/BaseFormField.vue"
export default {
  //     ,     computed
  //    mixin ,     
  props:['title', 'error', 'placeholder', 'value'],  //        -model ,    value
  components: {BaseFormField},
  computed:{
      dataValue: {
        get() {
          return this.value
        },
        set(value) {
            this.$emit('input',  value)
        }
      }
  }
}
